





















































import Vue from 'vue'
import { createMenu, getMenuInfo, Menu } from '@/services/menu'

interface MemuData {
  parentMenuList: []
  menuInfo: null | Menu
}

export default Vue.extend({
  props: {
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      form: {
        parentId: -1,
        name: '',
        href: '',
        icon: '',
        orderNum: 0,
        description: '',
        shown: true
      },
      parentMenuList: []
    }
  },
  created() {
    this.loadMenuInfo()
  },
  methods: {
    async loadMenuInfo() {
      const id = this.$route.params.id || -1
      const { data } = await getMenuInfo<MemuData>(id)
      if (data.code === '000000') {
        if (data.data.menuInfo) {
          this.form = data.data.menuInfo
        }
        this.parentMenuList = data.data.parentMenuList
      }
    },
    async onSubmit() {
      const { data } = await createMenu({
        ...this.form
      })
      if (data.code === '000000') {
        this.$message.success('提交成功')
        this.$router.back()
      } else {
        if (data.mesg) {
          this.$message.error(data.mesg)
        }
      }
    }
  }
})
